import { FC, ChangeEvent, useState } from 'react'
import PropTypes from 'prop-types'
import {
  Tooltip,
  Divider,
  Box,
  FormControl,
  InputLabel,
  Card,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Select,
  MenuItem,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material'

import Label from 'src/components/Label'
import { Member, MemberStatus } from 'src/models/member'
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone'
import OutputIcon from '@mui/icons-material/Output'
import { MEMBER_VALUES } from 'src/data/MemberValues'
import StarIcon from '@mui/icons-material/Star'
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount'

interface MembersTableProps {
  className?: string
  members: Member[]
  handleEdit(member: Member): void
  handleFire(member: Member): void
}

interface Filters {
  status?: MemberStatus
}

const getStatus = (member: Member): MemberStatus => {
  if (member.fee) {
    // 회비 납부하면 활동(ACTIVE)
    return 'ACTIVE'
  } else if (!member.fee && member.firedAt === null && member.deletedAt === null) {
    // 회비 납부 안하고, 탈퇴나 제적 상태가 아니라면 회비 미납부(NOT_FEE)
    return 'NOT_FEE'
  } else if (member.firedAt !== null) {
    // 제적된 회원인지?
    return 'FIRED'
  } else {
    return 'DELETED'
  }
}

const getStatusLabel = (memberStatus: MemberStatus): JSX.Element => {
  const map = {
    ACTIVE: {
      text: MEMBER_VALUES.status[0].display,
      color: 'success'
    },
    NOT_FEE: {
      text: MEMBER_VALUES.status[1].display,
      color: 'warning'
    },
    FIRED: {
      text: MEMBER_VALUES.status[2].display,
      color: 'error'
    },
    DELETED: {
      text: MEMBER_VALUES.status[3].display,
      color: 'error'
    }
  }

  const { text, color }: any = map[memberStatus]

  return (
    <Label color={color}>
      <Typography textAlign="center" noWrap>
        {text}
      </Typography>
    </Label>
  )
}

const applyFilters = (members: Member[], filters: Filters): Member[] => {
  return members.filter(member => {
    let matches = true

    if (filters.status && getStatus(member) !== filters.status) {
      matches = false
    }

    return matches
  })
}

const applyPagination = (members: Member[], page: number, limit: number): Member[] => {
  return members.slice(page * limit, page * limit + limit)
}

const MembersTable: FC<MembersTableProps> = ({ members, handleEdit, handleFire }) => {
  const [page, setPage] = useState<number>(0)
  const [limit, setLimit] = useState<number>(5)
  const [filters, setFilters] = useState<Filters>({
    status: null
  })

  const statusOptions = [
    {
      id: 'all',
      name: 'All'
    },
    {
      id: MEMBER_VALUES.status[0].value,
      name: MEMBER_VALUES.status[0].display
    },
    {
      id: MEMBER_VALUES.status[1].value,
      name: MEMBER_VALUES.status[1].display
    },
    {
      id: MEMBER_VALUES.status[2].value,
      name: MEMBER_VALUES.status[2].display
    },
    {
      id: MEMBER_VALUES.status[3].value,
      name: MEMBER_VALUES.status[3].display
    }
  ]

  const handleStatusChange = (e: ChangeEvent<HTMLInputElement>): void => {
    let value = null

    if (e.target.value !== 'all') {
      value = e.target.value
    }

    setFilters(prevFilters => ({
      ...prevFilters,
      status: value
    }))
  }

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage)
  }

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value))
  }

  const filteredMembers = applyFilters(members, filters)
  const paginatedMembers = applyPagination(filteredMembers, page, limit)
  const theme = useTheme()

  return (
    <Card>
      <CardHeader
        action={
          <Box width={150}>
            <FormControl fullWidth variant="outlined">
              <InputLabel>활동</InputLabel>
              <Select value={filters.status || 'all'} onChange={handleStatusChange} label="Status" autoWidth>
                {statusOptions.map(statusOption => (
                  <MenuItem key={statusOption.id} value={statusOption.id}>
                    {statusOption.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        }
        title="회원 명단"
      />

      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>이름</TableCell>
              <TableCell>학과</TableCell>
              <TableCell>학번</TableCell>
              <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                회원 등급
              </TableCell>
              <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                활동
              </TableCell>
              <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                회비
              </TableCell>
              <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                출석
              </TableCell>
              <TableCell align="center" sx={{ whiteSpace: 'nowrap' }}>
                비고
              </TableCell>
              <TableCell align="right" />
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedMembers.map(member => {
              return (
                <TableRow hover key={member.id}>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      <Box style={{ display: 'flex', alignItems: 'center' }}>
                        {member.name}
                        {member.permission === 'SUPERADMIN' && (
                          <StarIcon htmlColor="orange" sx={{ height: '20px', width: '20px' }} />
                        )}
                        {member.permission === 'ADMIN' && (
                          <SupervisorAccountIcon color="primary" sx={{ height: '20px', width: '20px' }} />
                        )}
                      </Box>
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                      {new Date(member.createdAt).toLocaleDateString()}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.department}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.studentId}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.level}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">{getStatusLabel(getStatus(member))}</TableCell>
                  <TableCell align="right">
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.fee ? 'O' : 'X'}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {`${member.attendance} / ${member.totalAttendance}`}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" sx={{ maxWidth: { xs: '100px', md: '12vw' } }}>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.note ? member.note : '-'}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="정보 수정" arrow onClick={() => handleEdit(member)}>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="제적" arrow onClick={() => handleFire(member)}>
                      <IconButton
                        sx={{
                          '&:hover': { background: theme.colors.error.lighter },
                          color: theme.palette.error.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <OutputIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredMembers.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
    </Card>
  )
}

MembersTable.propTypes = {
  members: PropTypes.array.isRequired
}

MembersTable.defaultProps = {
  members: []
}

export default MembersTable
