import {
  Box,
  Card,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Modal,
  Select,
  styled,
  TextareaAutosize,
  TextField,
  Typography
} from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Member, Permission } from 'src/models/member'
import { MEMBER_VALUES } from 'src/data/MemberValues'
import MembersTable from './MembersTable'
import StarIcon from '@mui/icons-material/Star'
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import EmailIcon from '@mui/icons-material/Email'
import PhoneIcon from '@mui/icons-material/Phone'
import dayjs from 'dayjs'
import FireDialog from './FireDialog'
import { useSnackbar } from 'notistack'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'

interface MemberObjForAPI extends Member {
  userId: number | undefined
  clubId: number
}

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

function MemberList() {
  const [memberList, setMemberList] = useState<Member[]>([])
  const [isModal, setIsModal] = useState<boolean>(false)
  const [editMember, setEditMember] = useState<Member>()
  const [stateForGetMembers, setStateForGetMembers] = useState<boolean>(false)
  const [isFireDialog, setIsFireDialog] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const params = useParams()
  const authUser = useAuthUser()
  const club = authUser()?.clubs.filter(c => c.id === parseInt(params.id))[0]
  const authHeader = useAuthHeader()

  const memberObjForAPI: MemberObjForAPI = Object.assign(
    { userId: editMember && editMember.id, clubId: parseInt(params.id) } as MemberObjForAPI,
    editMember
  )
  const urlForUpdate = `${process.env.REACT_APP_APIHOST}/club/member`

  const handleEdit = (member: Member): void => {
    setEditMember(member)
    setIsModal(true)
  }

  const handleModalClose = () => {
    setIsModal(false)
  }

  const handleSubmit = async () => {
    try {
      const result = await axios.patch(urlForUpdate, memberObjForAPI, {
        headers: { Authorization: authHeader() }
      })

      if (result.data?.message !== 'ok') throw new Error()

      enqueueSnackbar('성공적으로 수정되었습니다.', { variant: 'success' })
    } catch (e) {
      enqueueSnackbar(e.response?.data?.message, { variant: 'error' })
    }

    setStateForGetMembers(!stateForGetMembers)
    handleModalClose()
  }

  const handleFire = (member: Member): void => {
    setEditMember({ id: member.id, firedAt: dayjs(new Date()).toISOString(), ...editMember })

    setIsFireDialog(true)
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/member?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Member[]>) => {
        setMemberList(res.data)
      })
  }, [stateForGetMembers])

  return (
    <Card>
      <MembersTable members={memberList} handleEdit={handleEdit} handleFire={handleFire} />
      {editMember && (
        <Modal
          open={isModal}
          onClose={handleModalClose}
          aria-labelledby="회원 정보 수정"
          aria-describedby="수정 후 완료 버튼을 눌러주세요."
        >
          <Box sx={style}>
            <Typography variant="h3" fontWeight="bold" color="text.primary" display="inline" gutterBottom noWrap>
              {editMember.permission === 'SUPERADMIN' && (
                <StarIcon htmlColor="orange" sx={{ height: '20px', width: '20px' }} />
              )}
              {editMember.permission === 'ADMIN' && (
                <SupervisorAccountIcon color="primary" sx={{ height: '20px', width: '20px' }} />
              )}
              {editMember.name}
            </Typography>
            <Typography variant="body2" display="inline" color="text.secondary" noWrap>
              {`(${new Date(editMember.createdAt).toLocaleDateString()})`}
            </Typography>
            <Typography variant="body1" fontWeight="bold" color="text.secondary" noWrap>
              {`${editMember.studentId} ${editMember.department}`}
            </Typography>
            <Grid container mt={1}>
              <Grid item xs={12} md={6}>
                <Typography variant="body1" color="text.secondary" display="flex" alignItems="center">
                  <PhoneIcon sx={{ height: '1rem', width: '1rem', marginRight: '3px' }} />
                  {editMember.phoneNumber ? `${editMember.phoneNumber}` : '-'}
                </Typography>
                <Typography variant="body1" color="text.secondary" display="flex" alignItems="center">
                  <EmailIcon sx={{ height: '1rem', width: '1rem', marginRight: '3px' }} />
                  {`${editMember.email}`}
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography variant="caption" color="text.secondary" display="block">
                  {`가입 : ${new Date(editMember.createdAt).toLocaleDateString()}`}
                </Typography>
                {editMember.deletedAt && (
                  <Typography variant="caption" color="text.secondary" display="block">
                    {`탈퇴 : ${new Date(editMember.deletedAt).toLocaleDateString()}`}
                  </Typography>
                )}
                {editMember.firedAt && (
                  <Typography variant="caption" color="text.secondary" display="block">
                    {`제적 : ${new Date(editMember.firedAt).toLocaleDateString()}`}
                  </Typography>
                )}
              </Grid>
            </Grid>

            <Grid mt={1} container spacing={2}>
              <Grid item md={3} xs={12}>
                <FormControl fullWidth>
                  <TextField
                    label="등급"
                    id="level"
                    defaultValue={editMember.level}
                    onChange={e => {
                      memberObjForAPI.level = e.target.value
                    }}
                  ></TextField>
                </FormControl>
              </Grid>
              <Grid item md={3} xs={12}>
                <FormControl fullWidth>
                  <InputLabel id="feeLabel">회비</InputLabel>
                  <Select
                    labelId="feeLabel"
                    label="회비"
                    id="fee"
                    defaultValue={editMember.fee ? MEMBER_VALUES.fee[0].value : MEMBER_VALUES.fee[1].value}
                    onChange={e => {
                      memberObjForAPI.fee = e.target.value === 'O'
                    }}
                  >
                    {MEMBER_VALUES.fee.map(fee => {
                      return <MenuItem value={fee.value}>{fee.display}</MenuItem>
                    })}
                  </Select>
                </FormControl>
              </Grid>
              {club.level === 'SUPERADMIN' && (
                <Grid item md={3} xs={12}>
                  <FormControl fullWidth>
                    <InputLabel id="permissionLabel">운영진</InputLabel>
                    <Select
                      labelId="permissionLabel"
                      label="운영진"
                      id="permission"
                      defaultValue={editMember.permission}
                      onChange={e => {
                        memberObjForAPI.permission = e.target.value as Permission
                      }}
                    >
                      {MEMBER_VALUES.permission.map(permission => {
                        return <MenuItem value={permission.value}>{permission.display}</MenuItem>
                      })}
                    </Select>
                  </FormControl>
                </Grid>
              )}

              <Grid item md={12} xs={12}>
                <FormControl fullWidth>
                  <CustomTextareaAutosize
                    maxRows={4}
                    minRows={3}
                    defaultValue={editMember.note}
                    placeholder="비고(영문기준 최대 50자)"
                    onChange={e => {
                      memberObjForAPI.note = e.target.value
                    }}
                  />
                </FormControl>
              </Grid>
            </Grid>

            <CancelSubmitBtns handleSubmit={handleSubmit} handleCancel={handleModalClose} submitBtnText="수정 완료" />
          </Box>
        </Modal>
      )}
      <FireDialog open={isFireDialog} handleClose={() => setIsFireDialog(false)} handleFire={handleSubmit} />
    </Card>
  )
}

export default MemberList
